:root {
  --fs-smaller: 0.8rem;
  --fs-small: 0.9rem;
  --fs-base:1.1rem;
  --fs-big:1.2rem;
  --fs-bigger:1.5rem;


  --ff-title: "Josefin Sans", sans-serif;

  --color-beige: #F9F4E2;
  --color-grey-dark: rgb(88, 86, 86);
  --color-grey-light-100: #e4dddd;
  --color-grey-light-200: #6c6767;
  --color-brown: rgb(142, 110, 110);
  --color-blue-light: #f0f4f6;
}

html {
  height: 100%;
}

.App {
  display: flex;
  text-align: center;
  min-height: 100dvh;
  flex-direction: column;
  color: var(--color-grey-dark);
  max-width: 1000px;
  margin: auto;
  overflow-x: hidden;
}

.container {
  display: flex;
  margin: auto;

}

label {
 all: unset;
}

nav {
 padding: 10px;
 font-family: var(--ff-title);
 font-size: var(--fs-bigger);
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 white-space: nowrap;
}

nav .link-container {
  padding: 1rem;
}

nav .link {
  color: var(--color-grey-dark);
  text-decoration: none;
  margin: 10px;
  font-weight: bold;
}

.active, nav .link:not(.active):hover {
  filter: brightness(130%);
}

.active {
  filter: brightness(150%);
}

.farm-icon {
  width: 250px;
  height: 250px;
  border-radius: 150px;
}

  h1 {
    font-family: var(--ff-title);
    font-size: var(--fs-bigger);
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    font-weight: 300;
}

button {
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
  font-size: var(--fs-smaller);
  color: var(--color-grey-dark);
  background-color: white;
  border: solid 1px var(--color-brown) ;
}

button:hover {
  filter: brightness(0.9);
}

p {
  font-size: var(--fs-base);
}

.image-container {
  overflow: hidden;
  margin: 0;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container img {
 object-fit: cover;
 border-radius: 20px;
}

.image-container img:hover {
  transform: scale(1.07);
 }

 .text {
  text-align: left;
  line-height: 1.7rem;
  overflow-wrap: break-word;
  font-size: var(--fs-bigger);
  white-space: pre-line
 }

@media (max-width: 800px) {

  nav  {
    font-size: var(--fs-base);
}
}


